@import url(https://fonts.googleapis.com/css2?family=Raleway&family=Roboto:wght@300;400;700&display=swap);
.navbar{width:100%;position:fixed;left:0;top:0;background-color:black;color:white;padding:12px;z-index:10}.navbar__content{max-width:1200px;margin:0 auto;display:flex;align-items:center;justify-content:space-between}.navbar__title{font-size:3rem}.navbar__button{color:#999;font-size:1.7rem;text-transform:uppercase;letter-spacing:1px;background-color:transparent;border:none;font-weight:700;cursor:pointer;transition:0.15s ease;outline:none}.navbar__button:hover{color:white}

.header{margin-top:61px;height:50vh;min-height:400px;background-color:black;color:white;background-image:url(/static/media/background.236014bf.jpg);background-size:auto 100%;background-repeat:no-repeat;background-position:85% -20px}.header__content{max-width:1200px;height:100%;display:flex;flex-direction:column;justify-content:center;margin:0 auto;font-size:6rem;font-family:'Raleway', sans-serif;font-weight:400}.header__content p:last-child{padding-left:150px}

.search{padding:0 20px}.search form{display:flex}.search input{width:100%;max-width:400px;padding:10px;outline:none;font-size:1.4rem;border:none;background-color:#fff}.search button{padding:0 10px;display:flex;align-items:center;justify-content:center;font-size:1.4rem;text-transform:uppercase;cursor:pointer;background-color:#fff;border:none;border-left:1px solid #333;outline:none;font-weight:700}.search button img{width:18px}.search button:hover{background-color:#f1f1f1}

.homepage{max-width:1200px;margin:0 auto;padding:200px 0;color:white}.homepage__login{text-align:center;padding-top:80px}.homepage__login-text{font-size:4rem;text-align:center;margin-bottom:36px;font-weight:normal}.homepage__login-button{color:#999;background-color:transparent;padding:20px 70px;font-size:2rem;text-transform:uppercase;border:2px solid #333;font-weight:bold;letter-spacing:1px;cursor:pointer;transition:0.15s ease;outline:none}.homepage__login-button:hover{color:white;border:2px solid white}.homepage__select-conainer{padding-top:80px;display:flex}.homepage__select{position:relative;width:calc(25% - 20px);margin:0 20px;border-radius:10px;cursor:pointer;-webkit-filter:grayscale(0.9);filter:grayscale(0.9);margin-bottom:50px}.homepage__select::after{content:'';display:block;padding-top:55%}.homepage__select:hover{-webkit-filter:grayscale(0);filter:grayscale(0)}.homepage__select.active{-webkit-filter:grayscale(0);filter:grayscale(0)}.homepage__select--characters{background-color:#00897b}.homepage__select--planets{background-color:#ff8f00}.homepage__select--species{background-color:#880e4f}.homepage__select--starships{background-color:#283593}.homepage__select-iamge{position:absolute}.homepage__select-iamge img{width:100%}.homepage__select-title{position:absolute;bottom:10px;left:10px;font-size:1.8rem}.homepage__select--characters .homepage__select-iamge{right:-10px;bottom:-25px;width:45%}.homepage__select--planets .homepage__select-iamge{right:-20px;top:-25px;width:60%}.homepage__select--species .homepage__select-iamge{right:3px;bottom:10px;width:55%}.homepage__select--starships .homepage__select-iamge{right:-35px;top:-15px;width:90%}.homepage__cards{padding:60px 20px;display:flex;flex-wrap:wrap;justify-content:center}.homepage__card{width:260px;margin:15px;border:2px solid #333;border-radius:5px;background-color:rgba(51,51,51,0.3)}.homepage__card-name{padding:0 20px;height:200px;display:flex;align-items:center;justify-content:center;text-align:center;flex-wrap:wrap;font-size:3rem;font-family:'Raleway', sans-serif;font-weight:700;border-bottom:2px solid #333}.homepage__card-content{padding:20px}.homepage__card-content p{font-size:1.4rem;margin-bottom:10px}.homepage__card-content p span{font-weight:780}.homepage__card-content a{font-size:1.2rem;color:red;color:#6978e7}.homepage__cards-empty{font-weight:300;font-size:5rem;margin-top:60px}

.details{max-width:1200px;margin:0 auto;padding:80px 0 160px}.details__path{color:white;font-size:2rem}.details__path a{color:white}.details__content{margin-top:80px}.details__element{color:white;font-size:2.4rem;font-weight:bold;margin-bottom:16px}.details__element span{margin-right:10px;font-weight:normal}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:10px;font-family:'Roboto', sans-serif;background-color:black}.main{position:relative}.main__background{background-image:url(/static/media/stars.202e2aef.jpg);background-color:black;width:100%;height:100%;position:absolute;left:0;top:0;background-repeat:no-repeat;background-size:cover;background-position:center;background-attachment:fixed;z-index:-1}.main__background::after{content:'';position:absolute;width:100%;height:100%;left:0;top:0;background-color:rgba(0,0,0,0.5);background-image:linear-gradient(180deg, #000 0%, rgba(0,0,0,0) 150px, rgba(0,0,0,0) 100%);z-index:5}.spinner{display:flex;justify-content:center;padding:60px 0}

